import { AdapterResponse } from '../../DTO/Server/Common/AdapterResponse';
import { AdapterPath } from '../../DTO/Server/Common/AdapterPath';
import { Injectable } from '@angular/core';
//import { Session } from '../../session/session';
import { LogManager } from '../log-manager/logManager';
import { InternalRPCManager } from './InternalRPCManager';
//import { LoginManager } from '../login-manager/LoginManager';
//import { Constants } from '../constants/constants';
import { JSONStoreManager } from '../jsonstore-manager/jsonstoremanager';
import { Utils } from '../../utils/utils';

@Injectable()
export class RemoteCallsManager {

  requestTimeout: number = 30000;
  connected: boolean = null;
  heartbeatInitialized: boolean = false;
  heartbeatInterval: number = 15000;
  lastOnlineTime: number = -1;

  constructor(public logger: LogManager, 
              //public session: Session, 
              public internal: InternalRPCManager, 
              //public login: LoginManager, 
              public jsonStoreManager: JSONStoreManager) {
  }
  
  /**
   * gets the flag that indicates if the app can reach the adapters layer
   */
  public isConnected(): boolean {
    if (this.connected == null) {
      this.setIsConnected(false);
    }
    return this.connected;
  }
  
  /**
   * sets the flag with the current isConnect value. If it differs from the last one a onlinestatuschange event is raised
   * @param isConn 
   */
  private setIsConnected(isConn: boolean) {
    var old = this.connected;
    this.connected = isConn;
    if (isConn)
      this.lastOnlineTime = new Date().getTime();
    if (old != isConn) {
      document.dispatchEvent(new Event('onlinestatuschange'));
    }
    old = null;
  }

  public invokeAdapter(adapter: AdapterPath, params, successCallback: (response: AdapterResponse<any>) => any,
    failCallback: (response: AdapterResponse<any>) => any,
    offlineCallback: (lastOnlineTime: number) => any,
    loginNeededCallback: () => any) {
    this.invokeAdapterWithTimeout(adapter, params, 15000, false, successCallback, failCallback, offlineCallback, loginNeededCallback, null);
  }

 public successInternal (response: AdapterResponse<any>,  successCallback)  {
      this.setIsConnected(true);
      successCallback(response);
    }
    

  public invokeAdapterWithTimeout(adapter: AdapterPath, params, timeout: Number, forceInvokation: boolean, successCallback: (response: AdapterResponse<any>) => any, failCallback: (response: AdapterResponse<any>) => any, offlineCallback: (lastOnlineTime: number) => any, loginNeededCallback: () => any, contextValues: any) {
    this.jsonStoreManager.updateLastSessionRefreshTime(()=>{ }, (err)=>{ this.logger.error("invokeAdapterWithTimeout: failed to update last session time: " + err); });
   
    var successInternal = (response: AdapterResponse<any>) => {
      this.setIsConnected(true);
      successCallback(response);
      successInternal = null;
    }

    var offlineInternal = () => {
      this.setIsConnected(false);
      offlineCallback(this.lastOnlineTime);
      offlineInternal = null;
    }

    //local method to properly invoke the adapter basing of invocation method
    var invoke = () => {
      if (adapter.isPost)
        this.internal.invokeAdapterInternalPost(adapter, params, timeout, successInternal, failCallback, offlineInternal, contextValues);
      else
        this.internal.invokeAdapterInternalGet(adapter, params, timeout, successInternal, failCallback, offlineInternal, contextValues);
    }
    
    /*
    if (this.connected == null || this.connected == true || forceInvokation == true) {
      if (this.session.LastLoginWasOffline) {
        //if last login was offline I need to verify user credentials so it performs a silent login, if it fails it will raise the loginNeededCallback
        this.login.silentLogin(invoke, loginNeededCallback);
      } else {
        //if last login was online it invokes the adapter (finally)
        invoke();
      }
    } else {
      //if the flag isConnected is false it means that in the last heartbeatInterval milliseconds the app couldn't reach the adapters layer, thus the invocation is aborted 
      //in order to spare the timeout time
      this.logger.info("Forced adapter fail call because of heartbeat");
      offlineInternal();
    }
    */
  }

  /**
   * this method starts the herathbeat process
   */
  public initialize(){
    if (this.heartbeatInitialized == false){
      this.logger.info("Heartbeat initialized");
      this.heartbeatInitialized = true;
      this.startHeartbeat();
    }
  }

  private startHeartbeat(){
    setInterval(()=>{
      this.hearthbeatTick();
     
    }, this.heartbeatInterval);
  }
  
  /**
   * every heartbeatInterval milliseconds the process tries to reach the adapters layer, it will then set the isConnected flag accordingly
   */
  private hearthbeatTick(){
   // var heartbeatTimeout = this.heartbeatInterval - 5000;
    //this.internal.invokeAdapterInternalGet(Constants.RemoteMethods.Hearthbeat, null,  20000, ()=>{ this.setIsConnected(true); }, ()=>{ }, ()=>{ this.setIsConnected(false); }, null);
  }
}
  


