import { AdapterResponse } from '../../DTO/Server/Common/AdapterResponse';
import { AdapterPath } from '../../DTO/Server/Common/AdapterPath';
import { Injectable } from '@angular/core';
import { LogManager } from '../log-manager/logManager';
import * as moment from 'moment';

declare var WLResourceRequest;

@Injectable()
export class InternalRPCManager {

    constructor(public logger: LogManager) {

    }

    private timeStamp(methodName: string): string {
      let timestamp: string = moment().toString();
      return timestamp + " : " + methodName;
    }
  

  /** 
   * invokes an adapter POST function 
   * 
  */
  public invokeAdapterInternalPost(adapter: AdapterPath, params, timeout: Number, successCallback: (response: AdapterResponse<any>) => any, failCallback: (response: AdapterResponse<any>) => any, offlineCallback: () => any, contextValues: any) {
    
    let timeStamp = this.timeStamp(adapter.Method);
    console.log(timeStamp);
    if(!params){
      params = {};
    }
    params["timeStamp"] = timeStamp;

    params = this.prepareParams(params, contextValues);



    this.logger.info("Calling POST Adapter : " + adapter.Adapter + " Class: " + adapter.Class + " Function: " + adapter.Method);
    var resourceRequest = new WLResourceRequest("/adapters/" + adapter.Adapter + "/" + adapter.Class + "/" + adapter.Method, WLResourceRequest.POST, timeout);
    if(adapter.Method ==  "insNoteCarattereEsecutivo") console.log(JSON.stringify(params));

    resourceRequest.sendFormParameters({ wrappedPayload: JSON.stringify(params) }).then((data) => {
      this.logger.info("Successfully Called Adapter : " + adapter.Adapter + " Class: " + adapter.Class + " Function: " + adapter.Method);
      successCallback(data.responseJSON);
    }).fail((data) => {
      this.logger.warning("Failed Call POST Adapter : " + adapter.Adapter + " Class: " + adapter.Class + " Function: " + adapter.Method + " Status: " + data.status);
      if (data.status == 0) { // if status == 0 the app is offline so it raises an offlineCallback
        offlineCallback();
      } else {
        var errorData = new AdapterResponse(); //otherwise the app received some error so the failCallback will be called
        errorData.errorMessage = data.errorCode + " - " + data.errorMsg;
        failCallback(errorData);
      }
    });
  }

  /** 
   * invokes an adapter GET function 
   * 
  */
  public invokeAdapterInternalGet(adapter: AdapterPath, params, timeout: Number, successCallback: (response: AdapterResponse<any>) => any, failCallback: (response: AdapterResponse<any>) => any, offlineCallback: () => any, contextValues: any) {

    let timeStamp = this.timeStamp(adapter.Method);
    console.log(timeStamp);
    if(!params){
      params = {};
    }
    params["timeStamp"] = timeStamp;
    params = this.prepareParams(params, contextValues);
    this.logger.info("Calling GET Adapter : " + adapter.Adapter + " Class: " + adapter.Class + " Function: " + adapter.Method);
    var resourceRequest = new WLResourceRequest("/adapters/" + adapter.Adapter + "/" + adapter.Class + "/" + adapter.Method, WLResourceRequest.GET, timeout);

    if (params != null) {
      var elem: any;
      for (elem in params) {//prepares the parameters as query params
        resourceRequest.setQueryParameter(elem, params[elem]);
      }
    }
    resourceRequest.send().then((data) => {
      this.logger.info("Successfully Called Adapter : " + adapter.Adapter + " Class: " + adapter.Class + " Function: " + adapter.Method);
      successCallback(data.responseJSON);
    }).fail((data) => {
      this.logger.warning("Failed Call GET Adapter : " + adapter.Adapter + " Class: " + adapter.Class + " Function: " + adapter.Method + " Status: " + data.status);
      if (data.status == 0) {// if status == 0 the app is offline so it raises an offlineCallback
        offlineCallback();
      } else {//otherwise the app received some error so the failCallback will be called
        var errorData = new AdapterResponse();
        errorData.errorMessage = data.errorCode + " - " + data.errorMsg;
        failCallback(errorData);
      }
    });
  }

  private prepareParams(params: any, contextValues: any) {
    if (contextValues == null)
      return params;

    for (var elem in params) {
      contextValues[elem] = params[elem];
    }
    return contextValues;
  }
}


