  decimal(name, value, precision){
    //manually launch change detection
    this.cdRef.detectChanges();
    let str = value.value
    if (precision == 0) {
      this.formPermessi.patchValue({[name]: (str)? str.replace('.', "") : ""})
    } else {
      this.formPermessi.patchValue({[name]: ""+this._decimalPipe.transform(+str,"1.1-"+precision).replace(/,/g, "")})
    } 
  }

  
  processForm(){
    this.LogManager.info("permessi - Crea_pdf");
    var nomePdfPartial = "Permessi";
    var model = "Permesso_Scheda";

    // Data per nome
    var today = new Date();
    var timeWrite = (today.getDate()) + "-" + (today.getMonth() + 1) + "-" + (today.getFullYear());
    var strDate = today.getDay()+""+today.getMonth()+""+today.getFullYear()+""+today.getHours()+""+today.getMinutes()+""+today.getSeconds();
    
    var self = this;
    
    // Object name
    this.nomePdf = model+"_"+this.ads.Id+"_"+strDate+".pdf";
    
    var item:
      {
        data: { today: string },        
        download: { needDownload: boolean, pdfName: string, ads: object },    
        dati: { form: Object },
        immagine: {  }
      }
      =
      {
        data: { today: timeWrite},       
        download: {
          needDownload: false,
          pdfName: this.nomePdf,
          ads: this.ads
        },
        dati: {
          form: {
            permesso: this.permesso,
            img: this.dataFoto,
            isComune: this.isComune()
          }
        },
        immagine: {  }  
      };
  
      // definizione callback success
      var pdfCreatedSuccess = (result: PdfResult) => {
  
        this.widgets.doWithDefaultLoader((loader) => {
              
          var deviceName = Utils.getDeviceEnvironment();

          if(deviceName!='preview') 
            fileUtil.createFile( this.ads.Id, "file",this.nomePdf,result.pdfObj,function(){

                // Send Data
                let itemId: ItemId = ItemId.create(self.ads.CodiceAds, self.ads.CodiceOdl);
                self.adsSync.execute(itemId, "uploadPermesso", self.permesso);

                // Upload PDF
                var name = fileUtil.getExternalStoragePath()+ self.ads.Id+"/file/"+ self.nomePdf;

                let allegato = {
                  File: " ", 
                  Path : " ",
                  NomeFile: " ",
                  OdL: " ",
                  TipoDoc: " "
                };
                allegato.File = '';
                allegato.Path = name;
                allegato.NomeFile = self.nomePdf;
                allegato.OdL = self.ads.CodiceOdl;
                allegato.TipoDoc = "Z_PDF";

                itemId = ItemId.create(self.ads.CodiceAds, self.ads.CodiceOdl);
                self.adsSync.execute(itemId, "uploadAllegati", allegato);

                // Upload immagini
                function _uploadImgArray(photoArray: Array<any>){
                  for(let foto of photoArray)
                  { 
                    let name = fileUtil.getExternalStoragePath()+ self.ads.Id+"/images/"+ foto.title+".jpg";

                    let allegato = {
                      File: " ", 
                      Path : " ",
                      NomeFile: " ",
                      OdL: " ",
                      TipoDoc: " "
                    };
                    allegato.File = '';
                    allegato.Path = name;
                    allegato.NomeFile = foto.title+".jpg";
                    allegato.OdL = self.ads.CodiceOdl;
                    allegato.TipoDoc = "Z_JPG";

                    let itemId: ItemId = ItemId.create(self.ads.CodiceAds, self.ads.CodiceOdl);
                    self.adsSync.execute(itemId, "uploadAllegati", allegato);
                  }  
                }

                _uploadImgArray(self.fotoGis );
                _uploadImgArray(self.fotoLarg);
                _uploadImgArray(self.fotoCons);
                _uploadImgArray(self.fotoPart);

                
                //--------------------
                var doc = [];
                var name = fileUtil.getExternalStoragePath()+self.ads.Id+"/file/"+self.nomePdf;
                for(let el of self.ads.dataDocument){
                  if(el.name != name) doc.push(el);
                }
                
                doc.push({name:name,tag:'doc'});
    
                let docUtils = new DocUtils();
    
                docUtils.addDocumentInMemory(name, result.base64);
                self.ads.dataDocument = doc;
                
                self.adsService.updateAds(self.ads,{dataDocument:self.ads.dataDocument},
                  function(){
                    console.log("save SchedaPermessi to jsonstore");
                    self.jsonStoreManager.storeSecureDataField({key:"JSON_DT"+self.ads.Id, dataType:"string"}, {JSONDATA: ''}, () => {}, () => {});
                    self.widgets.showToastStandardDuration("Documento SchedaPermessi salvato con successo");
                    loader.dismiss();
                    console.log('file creato')
                    self.ads.PermessiSaved.completed = true;
                
                    // returns to form then to AdsDetails
                    self.navCtrl.pop();
                    self.navCtrl.pop();
                
                  },
                  function(){
                    console.error("error on save SchedaPermessi to jsonstore");
                    self.jsonStoreManager.storeSecureDataField({key:"JSON_DT"+self.ads.Id, dataType:"string"}, {JSONDATA: ''}, () => {}, () => {});
                    self.widgets.showToastStandardDuration("Il salvataggio ha riscontrato degli errori");
                    loader.dismiss();
                    console.log('file creato')

                    // returns to form then to AdsDetails
                    self.navCtrl.pop();
                    self.navCtrl.pop();
                  });
                //--------------------

                /*
                self.navCtrl.popToRoot().then(()=>{
                  self.navCtrl.push(AdsdetailsPage,self.ads);
                  self = null;
                  result = null;
                });
                */
                
              },
            function(){  
              loader.dismiss();
              console.log('errore nella creazione del file')}
              )
        });

  
    }

      // Creazione pdf
      this.pdfManager.pdfCreate(model, item).then(
        url => {
          var bean: PrinterSignatureBean = new PrinterSignatureBean();
          bean.dataTemporaryUser = item;
          //bean.url = url.url;
          bean.url = url;
          bean.showDelegateFlag = false;
          bean.title = nomePdfPartial;
          bean.postprocessTypology = model;
          bean.pdfBean = item;
          this.blobPDF = url.blob;
          bean.succCallback = pdfCreatedSuccess;
          bean.adsToReturn = this.ads;
  
          this.navCtrl.push(
            PreviewPdfNoSignatures,
            { bean: bean }
          );
  
        }, (err) => {
          alert(err);
        }
      );
  
  }

