export class ModalScelta {

  scelta ;
  ads: Ads;
  email;
  email2;
  email3;
  pattern;


  constructor(public viewCtrl: ViewController, public navParams: NavParams, public adsService: AdsService,) {
     this.ads = navParams.get('ads');
     this.pattern = "[A-Za-z0-9._%+-]{3,}@[a-zA-Z]{3,}([.]{1}[a-zA-Z]{2,}|[.]{1}[a-zA-Z]{2,}[.]{1}[a-zA-Z]{2,})";
     this.scelta = 'digitale';
     if(this.ads.Cliente.Email && this.ads.Cliente.Email !== ""){
      this.email = this.ads.Cliente.Email;
    }

    
  
  }

  isEmptyMail(mail){
      if(mail==undefined || mail=='') return true;
      return false;
  }

  updateScelta(){
     if(this.scelta=='digitale') this.scelta = 'cartaceo';
     else this.scelta = 'digitale';
  }

  isValidMail(email, nomeMail){
       var EMAIL_REGEXP = /^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$/i;

        if (email != "" && (email.length <= 5 || !EMAIL_REGEXP.test(email))) {
            this.updateValidation(nomeMail, false);
            return false;
        }
        this.updateValidation(nomeMail, true);
      return true;
  }

  updateValidation(mail, valid){
    /*
    if(mail=="mail1"){
      var element = <HTMLElement>document.getElementById('mail1').firstChild;
      if(valid==false)
        element.style["border-bottom-color"] = "red";
      if(valid)
         element.style["border-bottom-color"] = "green";
    }
    if(mail=="mail2"){
        var element = <HTMLElement>document.getElementById('mail2').firstChild;
      if(valid==false)
        element.style["border-bottom-color"] = "red";
      if(valid)
         element.style["border-bottom-color"] = "green";
    }
      if(mail=="mail3"){
        var element = <HTMLElement>document.getElementById('mail3').firstChild;
      if(valid==false)
        element.style["border-bottom-color"] = "red";
      if(valid)
         element.style["border-bottom-color"] = "green";
    }*/
  }

  // isEnabled(){
  //   if(this.scelta=='cartaceo') return true;
  //   else {
  //     let enabled: boolean = true;
  //     if(this.isEmptyMail(this.email)) enabled =  false;
  //     else if(this.isValidMail(this.email,'mail1')== false) enabled =  false;  
  //     else if(this.isEmptyMail(this.email2)&&this.isEmptyMail(this.email3)) enabled = true;
  //     else if(this.isValidMail(this.email2,'mail2')== false) enabled = false;  
  //     else if(this.isEmptyMail(this.email3)) enabled =  true;
  //     else if(this.isValidMail(this.email3,'mail3')== false) enabled =  false;
  //     return enabled;
  //   }
  // }

  registraScelta(){
    if(this.scelta=="digitale"){
      if(this.isEmptyMail(this.email) || this.isValidMail(this.email,'mail1')== false){
        alert("Devi inserire almeno un indirizzo email valido per proseguire");
        return;
      }
      
      this.ads.Cliente.Email = this.email;
      if(this.isEmptyMail(this.email2)!=true)  this.ads.Cliente.Email += ";"+this.email2;
      if(this.isEmptyMail(this.email3)!=true)   this.ads.Cliente.Email += ";"+this.email3;
    }
    else {
      this.ads.Cliente.Email = undefined;
      this.email = undefined;
    }
    this.ads.Scelta = true;
    this.adsService.updateAds(this.ads, {Scelta: true}, () => {}, (err) => {
       
    });
       this.dismiss(true);

  }

  dismiss(isUpdate) {

    if(isUpdate==undefined)  {
      this.viewCtrl.dismiss();
      return;
  }
  
    this.viewCtrl.dismiss({ "update": isUpdate });
  }

}






