export class ModalSyncStatus {

  inQueue = [];
  ads: Ads;
  connectionStatus: boolean;

  refreshStart: boolean = false;

  constructor(public viewCtrl: ViewController, public navParams: NavParams, 
              public zone: NgZone, 
              //public remoteCallsManager: RemoteCallsManager, 
              public adsSync: AdsSync ) {
    
    if(navParams.data.ads) {
      this.ads = navParams.data.ads;
    }

    this.refreshStart = true;

    this.refreshQueue();
    
   
  }

  refreshData() {
   /*
    if(this.remoteCallsManager.isConnected()==true) {
      this.connectionStatus = true;
    } else{
      this.connectionStatus = false;
    }
    
    this.inQueue = [];
    this.adsSync._queueTemp.toArray().forEach(itm => {
      if(itm.itemId){
        if(itm.itemId.codiceAds === this.ads.CodiceAds || itm.itemId.codiceOdl === this.ads.CodiceOdl){
          this.inQueue.push(itm);
          
        }
      }
      
    });
    */
  }

  refreshQueue(){
    if(this.refreshStart){
      this.refreshData();
      setTimeout(() => {
      //console.log("Refreshing network data");
      this.refreshQueue();
      }, 5000);
    }
  }

  getConnectionStatus(){
     if(this.connectionStatus){
      return "Connesso";
     }else{
       return "OFFLINE";
     }
  }


  dismiss(isUpdate) {
    this.refreshStart = false;
    if(isUpdate==undefined){
       this.viewCtrl.dismiss();
       return;
    }
    
    this.viewCtrl.dismiss();
  }

}

