export class ModalEditTime {

  ore;
  minuti;

  timeFilter: string = "";
  dateFilter: string = "";

  arrivoTime;

  constructor(public viewCtrl: ViewController, public navParams: NavParams, public zone: NgZone,  public LogManager: LogManager ) {
    this.ore = new Date().getHours();
    this.minuti = new Date().getMinutes();

    if(this.ore<10){
      this.ore = '0'+this.ore;
    }
      
    else {
      this.ore = String(this.ore);
    }
     
    if(this.minuti<10) {
      this.minuti = '0'+this.minuti;
    }
    else {
      this.minuti = String(this.minuti);
    }

   this.zone.run(() => {
      this.arrivoTime = this.ore+':'+this.minuti;
    });
  }


  dismiss(isUpdate) {
    if(isUpdate==undefined){
       this.viewCtrl.dismiss();
       return;
    }
    this.ore = this.arrivoTime.split(':')[0];
    this.minuti = this.arrivoTime.split(':')[1];
    this.viewCtrl.dismiss({"dateFilter": this.dateFilter, "update": isUpdate, "ore": this.ore, "minuti": this.minuti });
  }

}


