export class ModalEditIndirizzoLavoro {

  indirizzo_lavoro: Indirizzo;
  modificaIndirizzoLavoro: boolean = false;

  constructor(public viewCtrl: ViewController, public navParams: NavParams) {
    if(navParams.data.indirizzo_lavoro) {
      this.indirizzo_lavoro = navParams.data.indirizzo_lavoro;
    }
    
  }

  dismiss(isUpdate) {

    if(this.indirizzo_lavoro._civico==""||this.indirizzo_lavoro._civico==undefined){
      alert("E' necessario valorizzare il civico per proseguire");
      return;
    }

    if(isUpdate==undefined)  {
      this.viewCtrl.dismiss();
      return;
  }
    if(!this.modificaIndirizzoLavoro) {
      isUpdate = false;
    }
    this.indirizzo_lavoro.Via = this.indirizzo_lavoro.Via.toUpperCase();

    this.viewCtrl.dismiss({"indirizzo_lavoro": this.indirizzo_lavoro, "update": isUpdate });
  }

}



