    getLimitrofiIcon(item){
      let settoreMerceologico = this.getSettoreLimitrofi(item);
      let dettaglioMerceologico = this.getDettaglioLimitrofi(item);

      var icon: string = "";
      switch(settoreMerceologico) {
          case "ACQUA":
              icon = "water.png";
              break;
          case "GAS":
                icon = "gas.png";
                break;
          case "ENERGIA_ELETTRICA":
              icon = "energia.png";
              break;
          case "TLR":
              icon = "tlr.png";
              break;
          default:
              icon = "no_icon.png";
      }

      if(dettaglioMerceologico){
        switch(dettaglioMerceologico) {
          case "ACQUE_METEORICHE":
              icon = "water.png";
              break;
          case "ACQUEDOTTO_CIVILE":
              icon = "water.png";
              break;
          case "ACQUEDOTTO_INDUSTRIALE":
              icon = "water.png";
              break;
          case "ALTA_TENSIONE":
              icon = "energia.png";
              break;
          case "BASSA_TENSIONE":
              icon = "energia.png";
              break;
          case "FOGNATURA":
              icon = "fogna.png";
              break;
          case "GPL":
              icon = "gpl.png";
              break;
          case "MEDIA_TENSIONE":
              icon = "energia.png";
              break;
          case "MT_MISURA_BT":
              icon = "energia.png";
              break;
          case "TELERISCALDAMENTO":
              icon = "tlr.png";
              break;
        }
      }
      

      return icon;
    }

    getSettoreLimitrofi(item){
      let settore = item["settoreMerceologico"].split(";");
      return SettoreMerceologico[settore[0]];
    }

    getDettaglioLimitrofi(item){
      let dettaglio = item["settoreMerceologico"].split(";")[1];
      if(dettaglio){
        return DettaglioMerceologico[dettaglio];
      }
      return "";
    }

    invia_dati_adapter(){
      if(this.ads.Stato === Stato.SOPRALLUOGO_SOSPESO) {
        let alert = this.alertCtrl.create({
          title: 'Sopralluogo Sospeso',
          subTitle: 'Il sopralluogo  sospeso. Per chiudere il sopralluogo ed inviare i dati al server  necessario rimuovere la sospensione',
          buttons: ['OK']
        });
        alert.present();
        return;
      }
      // INVIO FORM VERBALE DI SOPRALLUOGO
        this.ads.cleanBase64Doc();
    
          this.LogManager.info('invio tutti i documenti');
          this.invia_documenti();

          this.LogManager.info('invio foto');
          this.invia_foto();

          let itemId: ItemId = ItemId.create(this.ads.CodiceAds, this.ads.CodiceOdl);
            
          if(this.ads.NoteEsecutive && this.ads.NoteEsecutive !== null && this.ads.NoteEsecutive !== ""){
            this.adsSync.execute(itemId, "insNoteCarattereEsecutivo", {
              note: this.ads.NoteEsecutive,
              OdL: this.ads.CodiceOdl,
              TipoNota: "E"
            });
          }
          
            
          if(this.ads.NoteProgettuali && this.ads.NoteProgettuali !== null && this.ads.NoteProgettuali !== ""){
            this.adsSync.execute(itemId, "insNoteCarattereEsecutivo", {
              note: this.ads.NoteProgettuali,
              OdL: this.ads.CodiceOdl,
              TipoNota: "P"
            });
          }
            
          if(this.ads.Stato !== Stato.SOPRALLUOGO_ANNULLATO){
            this.adsSync.execute(itemId, "updateStatoPreventivo", {
              CodiceAds: this.ads.CodiceAds, 
              Odl: this.ads.CodiceOdl,
              Stato: "SOPRALLUOGO CONCLUSO",
            });

            this.ads.Stato = Stato.SOPRALLUOGO_CONCLUSO;


          //clean jsonstore disegno tecnico
            this.jsonStoreManager.storeSecureDataField({key:"JSON_DT"+this.ads.Id, dataType:"string"}, {JSONDATA: ''}, () => {}, () => {});
          


            this.adsService.updateAds(this.ads, {Stato: Stato.SOPRALLUOGO_CONCLUSO }, () => {}, () => {
              console.log("errore durante update sopralluogo concluso in jsonstore");
            })
            this.widgets.showToastStandardDuration("Invio dati dell'odl in corso");
          }
         

    }

    invia_documenti(){
       if(this.ads.dataDocument==undefined) return;
      for(let doc of this.ads.dataDocument){
        if(doc.tag=='doc' && doc.name.indexOf('Verbale') >-1 ||  doc.name.indexOf('PreventivoPDF') >-1){
        }
        else{
       
        var tmp = doc.name.split('/');
        //var dir1 = tmp[tmp.length - 3];
        //var dir2 = tmp[tmp.length - 2];
        var fileName = tmp[tmp.length - 1];

        let oggetto_allegato = {
                  File: " ", 
                  NomeFile: " ",
                  Path: " ",
                  OdL: " ",
                  TipoDoc: " "
                };
            oggetto_allegato.File = "";
            oggetto_allegato.Path = doc.name;
            oggetto_allegato.NomeFile = fileName;
            oggetto_allegato.OdL = this.ads.CodiceOdl;

        var tmp = doc.name.split('.');
        var estensione = tmp[tmp.length-1];     
        if(estensione.toUpperCase().indexOf('PDF')>-1) oggetto_allegato.TipoDoc = "Z_PDF";
        if(estensione.toUpperCase() == 'XLS') oggetto_allegato.TipoDoc = "Z_XLS";
        if(estensione.toUpperCase() == 'DOC') oggetto_allegato.TipoDoc = "Z_DOC";
        if(estensione.toUpperCase() == 'XLSX') oggetto_allegato.TipoDoc = "Z_XLSX";
        if(estensione.toUpperCase() == 'DOCX') oggetto_allegato.TipoDoc = "Z_DOCX";

        let itemId: ItemId = ItemId.create(this.ads.CodiceAds, this.ads.CodiceOdl);
        
        this.adsSync.execute(itemId, "uploadAllegati", oggetto_allegato);
        }
      }
    }


    invia_foto(){
      if(this.ads.dataFoto==undefined) return;
          for(let foto of this.ads.dataFoto)
            { 
              if(foto.tag=='altre'){
                var tmp = foto.name.split('/');
                //var dir1 = tmp[tmp.length - 3];
                //var dir2 = tmp[tmp.length - 2];
                var fileName = tmp[tmp.length - 1];

                let allegato = {
                  File: " ", 
                  Path : " ",
                  NomeFile: " ",
                  OdL: " ",
                  TipoDoc: " "
                };
                allegato.File = '';
                allegato.Path = foto.name;
                allegato.NomeFile = fileName;
                allegato.OdL = this.ads.CodiceOdl;
                allegato.TipoDoc = "Z_JPG";

                let itemId: ItemId = ItemId.create(this.ads.CodiceAds, this.ads.CodiceOdl);
                this.adsSync.execute(itemId, "uploadAllegati", allegato);
            }
          }
    }

