  _CaratteristicheString() {
    this.cString = [];

    for(let key in Caratteristiche.toJsObject(this.ads.Caratteristiche)) {
      if(this.ads.Caratteristiche[key]){
        var name = key.replace(/([A-Z])/g, ' $1').replace(/^./, function(str){ return str.toUpperCase(); }) +":";
        this.cString.push({key: name, value: this.ads.Caratteristiche[key]}); 
      }  
    }
  }

  
