  prendiInCarico() {
    Utils.checkConnection(() => {
            this.LogManager.info("adsDetails - prendiInCarico - checkConnection OK" );
            this.ads.PresoInCarico = true;
            this.ads.Stato = Stato.PRESO_IN_CARICO;

            let now = moment(Date.now());
            let presa_carico = now.format('DD-MM-YYYY H:mm');
            let itemId: ItemId = ItemId.create(this.ads.CodiceAds, this.ads.CodiceOdl);
            
            this.adsSync.execute(itemId, "updateStatoPreventivo", {
              CodiceAds: this.ads.CodiceAds, 
              Odl: this.ads.CodiceOdl, 
              Targa:this.targa,
              Stato: "PRESO IN CARICO",
            });

            
            //LO STATO DELL'ADS VIENE AGGIORNATO INDIPENDENTEMENTE DALL'ESITO DELLA CHIAMATA
            //PER POTER AGGIORNARLO IN APP ANCHE IN CASO DI OFFLINE
            this.adsService.updateAds(this.ads, 
                                      {Stato: Stato.PRESO_IN_CARICO,
                                      OraPresaInCarico: presa_carico,
                                      PresoInCarico: true
                                      },
                                      () => {}, 
                                      (err) => {});
           
    }, () => {
        this.LogManager.warning("adsDetails - prendiInCarico - checkConnection Assente KO" );
        let alert = this.alertCtrl.create({
            title: 'Connessione assente',
            subTitle: 'Connessione internet assente',
            buttons: ['OK']
          });
          alert.present();
        });
  }

  modificaArrivoSulPosto() {
    //var ore ;
    //var minuti;

 
    let modal = this.modalCtrl.create(ModalEditTime, { "dateFilter": this.ads.DataAppuntamento });

    modal.onDidDismiss(data => {
      this.openModal = undefined;
      this.LogManager.info("adsDetails - onDidDismiss" );
      if(data){
        if(data.update === true){
          var now = moment();
          now.set({h: data.ore, m: data.minuti, s:0 });
          this.ads.ArrivoSulPosto = now.format("DD-MM-YYYY HH:mm");
          let itemId: ItemId = ItemId.create(this.ads.CodiceAds, this.ads.CodiceOdl);
          
          this.adsSync.execute(itemId, "confermaArrivoSulPosto", {
            CodiceAds: this.ads.CodiceAds, 
            Odl: this.ads.CodiceOdl, 
            DataOraArrivo: now.format("YYYYMMDD HHmmss")
          });
                   
        }

        if(this.ads.IndirizzoSalvato==true){
          let itemId: ItemId = ItemId.create(this.ads.CodiceAds, this.ads.CodiceOdl);
          
            this.adsSync.execute(itemId, "updateStatoPreventivo", {
              CodiceAds: this.ads.CodiceAds, 
              Odl: this.ads.CodiceOdl, 
              Stato: "SOPRALLUOGO IN CORSO"
            });
          
            this.adsService.updateAds(this.ads, {Stato: Stato.SOPRALLUOGO_IN_CORSO,
                                                ArrivoSulPosto: this.ads.ArrivoSulPosto
                                              }, () => {}, (err) => {});  
               this.verClass = "btnEnabled";
          }


      }
    
          

    });
    modal.present();
    this.openModal = modal;
  }

