     navi_FormScelta() {
		  this.LogManager.info("adsDetails - navi_FormScelta ");
       if(this.verClass=="btnDisabled") return;

       if(this.ads.Scelta == true){
                if(this.ads.DoneSP == false)
                    this.navCtrl.push(SceltapagePage,this.ads);
                  else {
                    switch(this.ads.SettoreMerceologico)
                    {
                    case SettoreMerceologico.ACQUA:
                        this.navCtrl.push(Form1Page,this.ads);
                    break;    
                    case SettoreMerceologico.TLR:
                      this.navCtrl.push(Form1Page,this.ads);
                    break;                          
                    case SettoreMerceologico.GAS:
                     this.navCtrl.push(Form2Page,this.ads);
                    break;
                    case SettoreMerceologico.ENERGIA_ELETTRICA:
                      this.navCtrl.push(Form2Page,this.ads);
                    break;
                  }
                }
          }
        else{
          let modal = this.modalCtrl.create(ModalScelta,{ads:this.ads});

                modal.onDidDismiss(data => {
                    this.openModal = undefined;
                    if(data!=undefined){
                      if(this.ads.DoneSP == false)
                        this.navCtrl.push(SceltapagePage,this.ads);
                      else {
                        switch(this.ads.SettoreMerceologico)
                        {
                        case SettoreMerceologico.ACQUA:
                            this.navCtrl.push(Form1Page,this.ads);
                        break;    
                        case SettoreMerceologico.TLR:
                          this.navCtrl.push(Form1Page,this.ads);
                        break;                          
                        case SettoreMerceologico.GAS:
                        this.navCtrl.push(Form2Page,this.ads);
                        break;
                        case SettoreMerceologico.ENERGIA_ELETTRICA:
                          this.navCtrl.push(Form2Page,this.ads);
                        break;
                      }
                      }
                    }
                      
                });
                  modal.present();
                  this.openModal = modal;
            }

    
    } 

