    sospendiForzaMaggiore() {
        this.LogManager.info("adsDetails - sospendiForzaMaggiore");
       this.jsonStoreManager.getSecureDataField({key: `sospeso_${this.ads.Id}`, dataType: "string"}, (timeStampStart) => {
                    if(timeStampStart!= undefined && timeStampStart!=''){
                      
                      let alert = this.alertCtrl.create({
                        title: 'Errore',
                        subTitle: 'Hai gi sospeso questo ads precedentemente',
                        buttons: ['OK']
                        
                      }                      
                      );                      
                      alert.present();
                      
                    }
                    else {
                          let message = "ATTENZIONE, l'applicazione di questa causale ha importanti effetti sulla modalit di indennizzo al Cliente. Deve essere utilizzata SOLO per i casi espressamente previsti dalla normativa, di seguito richiamati.";
                          if(this.ads.SettoreMerceologico === SettoreMerceologico.ACQUA
                            || this.ads.DettaglioMerceologico === DettaglioMerceologico.FOGNATURA) {
                            message += `
                                cause di forza maggiore, intese come atti di autorit pubblica, eventi naturali eccezionali per 
                                i quali sia stato dichiarato lo stato di calamit dall'autorit competente, scioperi indetti senza 
                                il preavviso previsto dalla legge, mancato ottenimento di atti autorizzativi.
                            
                            `;
                          }
                          if(this.ads.SettoreMerceologico === SettoreMerceologico.GAS) {
                            message += ` cause di forza maggiore, intese come atti di autorit pubblica, eventi 
                            naturali eccezionali per i quali sia stato dichiarato lo stato di calamit dall'autorit 
                            competente, scioperi, mancato ottenimento di atti autorizzativi`;
                          }
                          if(this.ads.SettoreMerceologico === SettoreMerceologico.ENERGIA_ELETTRICA) {
                            message += ` cause di forza maggiore, intese come: interruzioni eccezionali, dovute a eventi 
                            eccezionali, a furti, atti di autorit pubblica quali ad esempio ordini di apertura delle linee 
                            per spegnimento di incendi o per motivi di sicurezza impartiti da Terna o da altri esercenti 
                            interconnessi, o interruzioni dovute a disalimentazioni programmate comunicate da Terna, o per 
                            azioni funzionali a garantire la sicureza del sistema elettrico e comunicate da Terna con preavviso 
                            di almeno 3 giorni lavorativi, o scioperi indetti senza il preavviso previsto dalla legge; sono inoltre 
                            attribuite a cause di forza maggiore le quote di durata di interruzione dovute a casi di 
                            sospensione o posticipazione delle operazioni di ripristino per motivi di sicurezza`;
                          }

                          let confirm = this.alertCtrl.create({
                          title: 'Sospendi Causa Forza Maggiore',
                          message: message,
                          buttons: [
                            {
                              text: 'Annulla',
                              handler: () => {
                                
                              }
                            },
                            {
                              text: 'OK',
                              handler: () => {
                                  document.getElementById('fabMenu').click();
                                var timeStamp = moment().toString();

                                let itemId: ItemId = ItemId.create(this.ads.CodiceAds, this.ads.CodiceOdl);
                                let tmp: Stato = this.ads.Stato;

                                this.zone.run(() => {
                                  this.ads.Sospeso = true;
                                  this.ads.StatoPrecedente = tmp;
                                  this.ads.Stato = Stato["SOPRALLUOGO_SOSPESO"];
                                  this.ads.ForzaMaggiore = true;
                                });
                                this.jsonStoreManager.storeSecureDataField({key: `sospeso_${this.ads.Id}`, dataType: "string"}, 
                                                                          {timeStamp: timeStamp}, () => {}, () => {})
                                this.adsService.updateAds(this.ads, {Sospeso: true, 
                                                                     StatoPrecedente: tmp,
                                                                     Stato: Stato.SOPRALLUOGO_SOSPESO,
                                                                     ForzaMaggiore: true
                                                                    },
                                                            (data) => {}, 
                                                            (err) => { 
                                                              this.LogManager.error("adsDetails - sospendiForzaMaggiore - " + 
                                                              "storeSecureDataField - updateAds - error", err );
                                                            });
                               
                                this.adsSync.execute(itemId, "updateStatoPreventivo", {
                                  CodiceAds: this.ads.CodiceAds, 
                                  Odl: this.ads.CodiceOdl, 
                                  Causale: "ZS01",
                                  Stato: "SOPRALLUOGO SOSPESO"
                                });

                           //   this.navCtrl.push(AdsdetailsPage,{ads:this.ads});

                              }
                            }
                          ]
                        });
                        confirm.present();

                    }

      }, () => {});
    }

